  .shopnow {
    width: 100%;
    margin: 40px auto 100px;
    border-top: 1px solid $color_celeste_approx;
    a.buy {
      display: block;
      width: 100px;
      height: 40px;
      line-height: 40px;
      background-color: $color_sunset_orange_approx;
      margin: 40px auto;
      color: $white;
      text-align: center;
    }
  }
  ul.eatlist {
    width: 1000px;
    margin-top: 0;
    li {
      width: 1000px;
      height: 430px;
      margin: 20px auto;
      .img {
        z-index: 1;
        width: 340px;
        height: 430px;
        float: left;
        display: inline;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        box-shadow: 0 0 0;
        transition: opacity 0.2s, box-shadow .2s;
        &:hover {
          opacity: .9;
          box-shadow: 0 0 7px gray;
        }
        &.right {
          float: right;
        }
      }
      .info {
        position: relative;
        float: left;
        display: inline;
        width: 320px;
        height: 430px;
        background: $concrete url(../images/eatbg.png) center center;
        background-size: cover;
        img {
          float: none;
          display: block;
          width: 130px;
          height: 148px;
          margin: 50px auto 20px;
        }
        h6 {
          text-align: center;
          line-height: 20px;
          width: 267px;
          font-size: 15px;
          &.text {
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            text-align: left;
            margin-top: 5px;
            color: #656565;
            font-size: 13px;
            height: 60px;
          }
        }
        .btns {
          position: absolute;
          bottom: 12px;
          width: 305px;
          padding: 0 10px;
          box-sizing: border-box;
          margin: 0 auto;
          border-top: 1px solid $color_celeste_approx;
          left: 0;
          right: 0;
          a {
            &.gotobuy {
              float: left;
              display: inline;
              width: 80px;
              height: 30px;
              line-height: 30px;
              color: $white;
              background-color: $color_sunset_orange_approx;
              text-align: center;
              font-size: 14px;
              margin-top: 15px;
            }
            &.plus {
              cursor: pointer;
              float: right;
              display: inline;
              width: 30px;
              height: 30px;
              color: $white;
              font-size: 22px;
              line-height: 27px;
              text-align: center;
              margin-top: 15px;
              background: $color_masala_approx;
            }
            &.mius {
              display: none;
              cursor: pointer;
              float: right;
              width: 30px;
              height: 30px;
              color: $white;
              font-size: 22px;
              line-height: 27px;
              text-align: center;
              margin-top: 15px;
              margin-right: 7px;
              background: $color_sunset_orange_approx;
            }
          }
        }
        p.price {
          font-size: 30px;
          text-align: center;
          line-height: 80px;
          position: absolute;
          width: 100%;
          bottom: 66px;
        }
      }
    }
  }
  img.pcg {
    display: block;
    width: 1000px;
    height: 355px;
    margin: 10px auto;
  }
}


